import { createContext, useContext, useState } from "react";
import {
  CelebrationContextType,
  CelebrationInformationType,
} from "../Types/types";
import IThemeContextProvider from "../Interfaces/Themes/IThemeContextProvider";

const CelebrationContext = createContext<CelebrationContextType | null>(null);
export const useCelebrationContext = () =>
  useContext(CelebrationContext) as CelebrationContextType;

export const CelebrationContextProvider: React.FC<IThemeContextProvider> = ({
  children,
}) => {
  const [currentCelebrant, setCurrentCelebrant] =
    useState<CelebrationInformationType>({
      celebrantsName: "Rafaella",
      celebrationType: "Mis Tres Aos",
      invitationMessage:
        "Estamos emocionados de invitarlos a la fiesta de cumpleaos de nuestra pequea Rafaella! Va a cumplir 3 aos y queremos celebrarlo en grande.<br />" +
        "Habr msica, juegos y muchas sorpresas ms. Adems, no te pierdas nuestra deliciosa comida y pastel.",
      longMessage:
        "Mi Querida Rafaella,<br />" +
        "Hoy, mientras te escribimos esta carta, t ests dando tus primeros pasos hacia un mundo lleno de aventuras y descubrimientos. Parece que fue ayer cuando llegaste a nuestras vidas, iluminando cada rincn con tu risa contagiosa y tus ojos curiosos que exploran el mundo que te rodea.<br />" +
        "Maana, celebraremos tu tercer cumpleaos. Tres aos de amor incondicional, de abrazos clidos y de momentos que atesoraremos por siempre en nuestros corazones. En este da especial, queremos invitarte a una fiesta que ser mucho ms que una celebracin. Ser un recordatorio de todo lo maravilloso que eres y de lo afortunados que somos de tenerte en nuestras vidas.<br />" +
        "Te prometemos que la fiesta estar llena de alegra y diversin, con juegos que despierten tu imaginacin y risas que llenen el aire. Habr msica que te haga bailar y comida deliciosa que deleitar tu paladar. Pero sobre todo, habr amor. Mucho amor, el mismo amor que nos ha unido desde el primer da y que seguir creciendo con cada paso que des.<br />" +
        "Queremos que sepas que este da es especial porque t eres especial. Eres la luz que brilla en nuestros das ms oscuros y la razn por la que nuestros corazones laten con tanta fuerza. Esperamos con ansias ver esa sonrisa radiante en tu rostro mientras celebramos juntos este nuevo ao de vida que comienza para ti.<br />" +
        "As que preprate, mi pequea aventurera, porque te espera una fiesta que quedar grabada en tu memoria para siempre. Estamos emocionados de compartir este da contigo y de crear recuerdos que perdurarn a lo largo del tiempo.<br />" +
        "Con todo nuestro amor,<br />" +
        "Tus Papas",
    });

  const updateCelebration = (celebrationInfo: CelebrationInformationType) => {
    setCurrentCelebrant(celebrationInfo);
  };

  return (
    <CelebrationContext.Provider
      value={{ currentCelebrant, updateCelebration }}
    >
      {children}
    </CelebrationContext.Provider>
  );
};
